.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.fill-screen {
  width: 100%;
  height: 100%;
}

.camera-view {
  background: #2a1102;
  position: relative;
}

.camera-view canvas {
  width: 100%;
  height: 100%;
  display: block;
}

.camera-view .shutter {
  box-sizing: border-box;
  height: 24vw;
  width: 24vw;
  border-radius: 24vw;
  border: 5px solid #f4971e;
  position: absolute;
  bottom: 20px;
  left: 38vw;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.125);
  max-width: 24vmin;
  max-height: 24vmin;
  outline: none;
}

.camera-view .flip {
  position: absolute;
  top: 5vw;
  right: 5vw;
  height: 16vw;
  width: 16vw;
}

.camera-view .shutter:active {
  background: rgba(255, 255, 255, 0.5);
}

.edit-page .cancel {
  position: absolute;
  top: 5vw;
  left: 5vw;
  height: 16vw;
  width: 16vw;
}

.edit-page button.send {
  position: absolute;
  bottom: 5vw;
  right: 5vw;
  height: 20vw;
  width: 20vw;
  background: #f4971e;
  border-radius: 20vw;
}

.edit-page button.send img {
  vertical-align: middle;
  display: inline-block;
  text-align: center;
  width: 70%;
  height: 70%;
}

button {
  box-sizing: border-box;
  outline: none;
  border: none;
  background: transparent;
}

button img {
  width: 100%;
  height: 100%;
  display: block;
}